
body{
  background-color: rgb(27, 27, 27);
  font-family: 'Roboto', sans-serif;
  margin:0px;
  color:white;
}
#headerSpan{
  background-color: rgb(14, 14, 14);
  width:100%;
  position: fixed;
}
#siteHeader{
  width:70%;
  margin:0 auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  color:white;
}
#siteHeader>h1{
  font-size:30px;
  margin-left:20px;
  margin-right:20px;
}
.siteHeaderLink{
  font-size:20px;
  padding:5px;
  color:white;
  text-decoration: none;
  transition: color ease-in-out .3s;
}
.siteHeaderLink:hover{
  color:red;
}
.active{
  color:green;
}
.foreground{
  width:68%;
  margin:0 auto;
  padding-top:100px;
}

.spaceBetween{
  justify-content: space-between;
}
.spaceAround{
  justify-content: space-around;
}
.highlighted{
  background-color:rgb(20, 20, 20);
}
.scaleChildren>*{
height:100%;
color:red;
}

.span{
  width:100%;
  display:flex;
}
.alignCenter{
  align-items: center;
}
.alignTop{
  align-items: baseline;
}
.justifyCenter{
  justify-content: center;
}
.flexcol{
  flex-direction: column;
}
.flexrow{
  flex-direction: row;
}
.span>*{
  margin-right:10px;
}
.noMargin{
  margin:0px;
}

.surface{
background-color: #333;
}
.pad{
  padding:10px;
}

.cellContainer{
width:100%;
background-color:rgba(0, 0, 0, 0.377);
height:1000px;
}

.cell{
  width:100%;
  display: flex;
  flex-direction: row;

 justify-content: center;
 padding-top:10px;
}
.cellContentContainer{
  width:50%;
  height:610px;
  display:flex;
  flex-direction: column;
  justify-content: space-between;
}
.cellContentContainer>p{
  padding-bottom:10px;
  min-height: 260px;
}

.cellImageContainer{
  width:350px;
  height:600px;
  margin-left:10px;
}
.cellImageContainer>img{
  width:100%;
  height:200px;
  background-color:rgb(22, 22, 22);
  border:0px;
}
#projectContainer{
  margin-top:68px;
}
.prompt{
width:100%;
position: fixed;
z-index: 1;
background-color:rgba(0, 0, 0, 0.527);
top:77px;
left: 50%;
transform: translate(-50%, +0%);
height:900px;
}
.promptClickableArea{
  z-index: 2;
  width:fit-content;
  margin:0 auto;
  width:800px;
}
.promptTextArea{
  width:100%;
  font-size:20px;
  padding:5px;
  margin:5px;
  resize: none;
  background-color:rgb(44, 44, 44);
  color:white;
}
.promptInput{
  width:100%;
  font-size:20px;
  padding:5px;
  margin:5px;
  
}
.promptClose{

  font-size:30px;
  padding:5px;
  margin:5px;
}

.selectContainer{
  top:-200px;
}
.selectOpenButton{
  font-size:30px;
  padding:5px;
  margin:5px;
}
.selectPopup{
  position: relative;
  top:-280px;
  background-color:gray;
  width:250px;
  min-height: 200px;
}

.pageHeader{
width:68%;
border-bottom:5px solid rgb(3, 3, 3);
padding-bottom:5px;
position: fixed;
top:77px;
background-color:rgba(0, 0, 0, 0.678);
}
.card{
  margin-top:70px;
  background-color:rgba(0, 0, 0, 0.377);
}
.card>*{
}
.cardTitle{
  border-bottom:1px solid rgb(255, 0, 0);
  width:fit-content;
  padding-bottom:5px;
}
.cardContent{
  width:100%;
  min-height: 300px;
  border-radius: 5px;

  font-size:20px;
}
.cardContent>*{
  margin-left:10px;
}
.cardOptions{
  height:40px;
  width:100%;
  display:flex;
  flex-direction:row;
  align-items: center;
  
}
.cardOptions>*{
  height:30px;
  margin:0px;
  background-color:rgba(0,0,0,0);
  border-style:none;
  color:white;
  font-size:18px;
  border-bottom:1px solid red;
}
#about_img_1{
  width:300px;
}
#about_img_2{
  width:300px;
}
#about_img_5{
  width:400px;
}
#aboutParagraphs>*{
  width:70%;
  font-size:20px;
  margin-bottom:5px;
  margin:0 auto;
}
#aboutParagraphs>div{
  display:flex;
  flex-direction: row;
}
#aboutParagraphs>div>*{
  display:flex;
  flex-direction: row;
  margin-left:5px;
}

footer{
  margin-top:100px;
}


.postHeader>div{
width:50%;
display:flex;
flex-direction: column;
align-items: baseline;
}
.postHeader>div>*{
  margin:0px;
}
.postHeader{

}
.postHeader>*{
  margin-left:10px;
}

#specialboi{
  
}
#fancyBox{
  position: fixed;
  top:77px;
  z-index: -1;
}

.listContainer{
  width:85%;
}
.ass{
  width:100%;
  min-height:50px;
  display: block;
}
.ass>div{
  width:fit-content;
  display:flex;
  float: left;
   color:white;
   margin:5px;

}
.ass>div>button{
   margin-left:5px;
}
.socialIcon{
  width:50px;
}